@import "swal";
@import "formly";

.percent-center{
    position: fixed;
    z-index: 10000;
    font-size: 1.2em;
    color: white;
    top: 49.5%;
    right: calc(50% - 25px);
    width: 50px;
    text-align: center;
}

.monospace{
    font-family: monospace;
}

input.invalid{
    background-color: #F2D7D5;
}

body.no-menu .main-header, 
body.no-menu .content-wrapper,
body.no-menu .main-footer
{
    margin-left: initial !important;
}

body{
    font-family: 'Roboto', sans-serif !important;
}

label.selector-input{
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400 !important;
    line-height: 1.5;
    background-color: initial;
    background-image: initial;
}

.one-ring .content-wrapper {
    background-color: #fffae6 !important;
}

.main-sidebar .version{
    position: absolute;
    color: white;
    z-index: 10000;
    right: 5px;
    font-size: 12px;
    opacity: 0.8;
    top: 2px;
}

.main-sidebar .sidebar .label-empresa{
    padding-left: 10px;
    color: white;
    opacity: 0.7;
}

.main-sidebar .nav li.nav-item a.nav-link i{
    font-size: 1.2em;
    width: 20px;
    text-align: center;
    margin-left: 20px;
    right: 1em;
    bottom: 10px;
}

.main-header{
    .navbar-nav{
        .btn{
            color: white;
            line-height: 24px;
        }
        .btn:hover,.btn:focus{
            color:gold;
        }
    }
}

.main-footer{
    padding-top: 5px;
}

.card-body.table-responsive{
    th.right, td.right{
        text-align: right;
    }
}

.user-panel{
    margin-top: 5px !important;
    .info{
        height: 40px;
        line-height: 16px;
    }
    p{
        color: white;
        opacity: 0.8;
        small{
            color: white;
        }
    }
}

.brand-link{
    img{
        position: absolute;
    }
    span{
        margin-left: 10px;
    }
}

.angucomplete-dropdown {
    width: 90%;
    -webkit-box-shadow: 5px 5px 5px -1px rgba(0,0,0,0.2);
    box-shadow: 5px 5px 5px -1px rgba(0,0,0,0.2);
}

.search-pexels{
    .card-img-top{
        width: auto;
        height: 160px;
        object-fit: cover;
    }
    .card-img-overlay{
        cursor: pointer;
        background: rgba(0,0,0,0.25);
    }
    .card-img-overlay:hover{
        background: initial;
    }
}

.google-map-position{
	.form-group-map{
		position: relative;
	}
	.map{
		height: 260px;
	}
	.map-icon{
		height: 26px;
		width: 26px;
		position: absolute;
		top: calc(50% - 13px);
		right: calc(50% - 13px);
		pointer-events: none;
	}
	textarea{
		height: 90px;
		resize: none;
	}
}

.top-progress{
    position: fixed;
    z-index: 100000;
    top: 0;
    left: 0;
    width: 100%;
    height: 8px;
}

.navbar-nav .nav-link.btn-default{
    color: black;
}

.note-editable p{
    margin-bottom: 5px;
}

.cursor-pointer{
    cursor: pointer;
}

.toast .toast-message{
    line-height: 15px;
}

.mb-100{ margin-bottom: 100px !important; }
.mb-120{ margin-bottom: 120px !important; }
.mb-150{ margin-bottom: 150px !important; }
.mb-200{ margin-bottom: 200px !important; }
.mb-300{ margin-bottom: 200px !important; }

a.nav-link{
    cursor: pointer;
}

a[disabled]{
    color: black;
    opacity: 0.1;
    cursor: not-allowed !important;
}

badge-true-false.link .badge{
    cursor: pointer;
}

.link{
    color :#007bff;
    cursor: pointer;
}

.link[disabled]{
    color: black;
    opacity: 0.1;
    cursor: not-allowed !important;
}

.modal-dialog{
    iframe, .ekko-lightbox-item.show{
        height: 100%;
        width: 100%;
    }
}

.note-editable * {
    line-height: inherit!important;
    font-size: initial!important;
 }

 @media (max-width: 576px){
     .hide-xs{
         display: none;
     }
     .card-body{
        padding: 15px 5px;
     }
 }

 @media (min-width: 577px) and (max-width: 768px){
    .hide-sm{
        display: none;
    }
}


@media (min-width: 769px) and (max-width: 992px){
    .hide-md{
        display: none;
    }
}

@media (min-width: 993px) and (max-width: 1200px){
    .hide-lg{
        display: none;
    }
}

@media (min-width: 1201px){
    .hide-xl{
        display: none;
    }
}

.width-50{ width: 50px; }
.width-80{ width: 80px; }
.width-100{ width: 100px; }

.mt-30{ margin-top: 30px; }

.popover {
    line-height: 1.25 !important;
    background-color: floralwhite;
}

.flex-end{
    justify-content: flex-end;
}

.swal2-icon.swal2-question.swal2-icon-show{
    font-size: 1em;
}

.font-monospace{
    font-family: monospace;
    font-size: 1.15em;
}

.position-relative{
    position: relative;
}

.help-tip{
    opacity: 0.4;
    cursor: help;
}

.help-tip:hover{
    opacity: 1;
}

.vertical-align-initial{
    vertical-align: initial !important;
}

.bold{
    font-weight: bold;
}

.opacity-0{
    opacity: 0;
}

.opacity-25{
    opacity: 0.25;
}

.opacity-50{
    opacity: 0.50;
}

.login-page{
    p.version{
        position: absolute;
        bottom: 0;
        font-family: monospace;
        text-align: center;
        font-size: 0.6em;
    }
}

table .center{
    text-align: center;
}

table .right{
    text-align: right;
}

div.selector-container.disabled{
    background: #e9ecef;
    color: black;
    opacity: .8;
}